@mixin css
{
    nodes-palette
    {
        display: flex;

        .editor-component
        {
            margin: 2px 10px;
            padding: 4px 8px;

            border-radius: 6px;
            border: 1px solid var(--dynamic-nodes-palette-editor-component-border);
            background-color: var(--dynamic-nodes-palette-editor-component-background);
        }

        .editor-component.used
        {
            opacity: .5;
            cursor: not-allowed;
        }

        .editor-component-name
        {
            font-weight: 500;
        }

        .editor-component-description
        {
            margin-left: 4px;
            margin-top: 2px;
            font-size: .8em;
        }

        .palette-droplist layout-editor-drag-placeholder
        {
            display: none;
        }
    }
}
